import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
import PrivatePortfolio from './PrivatePortfolio';
import PublicPortfolio from './PublicPortfolio';

const PortfolioLoader = props => {
	const pID = useParams().pID;
	const [data, setData] = useState(null);
	const [isLoading, setIsLoading] = useState(true);
	const [error, setError] = useState(null);

	useEffect(() => {
		axios.get(`https://api.parqet.com/v1/portfolios/${pID}`)
	    .then(response => {
	    	setData(response.data.holdings);
	      	setIsLoading(false);
	    })
	    .catch(error => {
	      	setError(error);
	      	setIsLoading(false);
	    });
	}, [pID]);
	
	if (isLoading) return <p>Loading...</p>;

	if (error) return <p>{error.message}</p>;

	let isPublic = false;
	if(isPublic) return (<PublicPortfolio pID={pID}/>)

	return(<PrivatePortfolio holdings={data}/>);
}

export default PortfolioLoader;
