import { useState } from 'react';
import { TextField, IconButton } from '@material-ui/core';
import { NavigateNext as SearchIcon } from '@mui/icons-material';
import { useNavigate } from "react-router-dom";
import { ThemeProvider } from '@material-ui/core/styles';

import { useCallback } from "react";
import Particles from "react-particles";
import { loadFull } from "tsparticles";

import { noSelect, particlesJsOptions, muiTheme } from './usefulStuff.js';

import Styled from "styled-components";

const LandingPage = props => {
	const navigate = useNavigate();
	const [ value, setValue ] = useState("");
	const [ error, setError ] = useState(false);
	
	const isValidPID = () => /^[0-9a-z]{24}$/.test(value);

	const onEnterPressed = ev => {
		if(ev.key === 'Enter') {  
			ev.preventDefault();
			onSubmit();
		}
	}

	const onSubmit = () => isValidPID() ? navigate(`/parqet-deanonymize/p/${value}`) : setError(true);

	const particlesInit = useCallback(
		async engine => await loadFull(engine),
    	[]
    );

    const particlesLoaded = useCallback(
    	async container => await console.log(container), 
      	[]
    );

	return (
		<ThemeProvider theme={muiTheme}>
		
			<div style={{zIndex: -1}}>
				<Particles id="tsparticles" init={particlesInit} loaded={particlesLoaded} options={particlesJsOptions} />
			</div>	
			
			<p style={css.heading}>
				parqet-deanonymize
			</p>
			<p style={css.subHeading}> 
				deanonymize user funds using  Math  
			</p>
			
			<css.inputWrapper> {/* is schaisse, aber immerhin funktionieren queries */}
				<div style={css.searchWrapper}>
					<TextField
						label="enter portfolio-ID"
						onChange={ev =>{ setError(false); setValue(ev.target.value);}}
						value={value}
						onKeyPress={onEnterPressed}
						error={error}
						helperText={error ? "this is not a valid Portfolio-ID :(" : ""}			
						style = {css.textField}	
					/>

					<IconButton onClick={onSubmit} style={css.iconButton}>
						<SearchIcon/>
					</IconButton>
				</div>
				
				<a style={css.helperText}>
					  what is the portfolio-ID? 
				</a>
			</css.inputWrapper>
			
		</ThemeProvider>
	);
}

const css = {
	heading: {
		color: "#ffffff", 
		fontSize: "calc(2.5vw + 1.4rem)", 
		margin: "0 0 1vh 0", 
		...noSelect 
	},
	subHeading: {
		color: "#ffffff", 
		fontSize: "calc(0.8vw + 0.8rem)", 
		margin: "0 0 200px 0", 
		...noSelect 
	},
	inputWrapper: Styled.div`
		background: #26B6AA22;
		box-shadow: 0 0 10px 10px #26B6AA22; 
		display: flex;
		flex-direction: column; 
		width: 85%;
		max-width: 400px; 
		align-items: center;

		@media only screen and (max-width: 768px) {
		    bottom: 170px;
		    position: fixed;
		}
    `,
    searchWrapper: {
    	width: "98%",
    	display: "flex",
    	flexDirection: "row"	
    },
	textField: { 
		width: "calc(100% - 20px)", 
		margin: "0px 5px 20px 10px" 
	},
	iconButton: { 
		width: "44px", 
		height: "44px", 
		marginTop: "10px"
	},
	helperText: {
		color: "#ffffff", 
		cursor: "pointer",
		margin: "auto auto auto 12px",
		...noSelect 
	}
}

export default LandingPage;
